




























import Footer from "@/components/Footer.vue";
import AboutView from "@/views/AboutView.vue";
import ApplicationsView from "@/views/ApplicationsView.vue";
import IntroView from "@/views/IntroView.vue";
import PricingView from "@/views/PricingView.vue";
import Vue from "vue";
import Component from "vue-class-component";
import CaptureView from "./views/CaptureView.vue";

interface ISlide {
  component: Vue;
  id: string;
  name?: string;
  visible: boolean;
  active: boolean;
}

@Component({
  data: () => ({
    drawer: null,
    slides: [
      {
        component: IntroView,
        id: "intro",
        visible: false,
        active: false
      },
      {
        component: CaptureView,
        name: "RF Capture",
        id: "rf-capture",
        visible: false,
        active: false
      },
      {
        component: ApplicationsView,
        name: "Applications",
        id: "applications",
        visible: false,
        active: false
      },
      {
        component: PricingView,
        name: "Pricing",
        id: "pricing",
        visible: false,
        active: false
      },
      {
        component: AboutView,
        id: "about",
        name: "About",
        visible: false,
        active: false
      }
    ]
  }),
  components: {
    Footer,
    AboutView,
    PricingView,
    ApplicationsView,
    IntroView,
    CaptureView
  }
})
export default class App extends Vue {
  public slides: ISlide[] = [];

  private isActiveSlide(slide: ISlide) {
    const active = this.slides.filter((s) => {
      return s.active;
    });
    if (active.length === 1) {
      return true;
    } else {
      return active[1].id === slide.id;
    }
  }

  private visibilityChanged(isVisible: boolean, entry: { target: Element }) {
    const slide = this.slides.find((value, index) => {
      return entry.target.id === value.id;
    });
    if (slide) {
      slide.visible = isVisible;

      slide.active = isVisible;
    }
  }
}
